<template>
    <div>
        <div class="row">
            <p class="tit">注册</p>
        </div>
        <!-- <div class="row">
            <p></p>
        </div> -->
        <div class="row">
            <el-input v-model="options.username" placeholder="用户名" :maxlength="12"></el-input>
        </div>
        <div class="row row2">
            <el-input v-model="options.pwd" placeholder="密码" type="password"></el-input>
        </div>
        <div class="row">
            <el-input v-model="repwd" placeholder="确认密码" type="password"></el-input>
        </div>
        <div class="row">
            <el-button type="primary" class="sure" @click="register">确定</el-button>
        </div>
        <div class="row">
            <router-link class="go-login" tag="span" to="/login">登录</router-link>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                repwd: '',
                options: {
                    username: '',
                    pwd: ''
                }
            }
        },
        methods: {
            register() {
                if (!this.options.username) {
                    return this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    })
                }
                if (!this.options.pwd) {
                    return this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    })
                }
                if (!this.repwd) {
                    return this.$message({
                        message: '请确认你的密码',
                        type: 'warning'
                    })
                }
                if (!this.options.username.match(/^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){4,19}$/)) {
                    return this.$message({
                        message: '用户名限制5-20个，以字母开头，可带数字、“_”、“.”',
                        type: 'warning'
                    })
                }
                if (this.options.pwd != this.repwd) {
                    return this.$message({
                        message: '两次密码不一致',
                        type: 'warning'
                    })
                }
                if (!this.options.pwd.match(/^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z\d]+$/)) {
                    return this.$message({
                        message: '密码过于简单，请使用字母和数字的组合',
                        type: 'warning'
                    })
                }
                this.axios.post(apiUrl + '/eatApi/user/register', this.options).then(res => {
                    if (res.data.msgcode == 1) {
                        this.$message({
                            message: '注册成功',
                            type: 'success'
                        })
                        this.$router.push('/login')
                    } else {
                        this.$message({
                            message: res.data.msg,
                            type: 'warning'
                        })
                    }
                })
            }
        },
        mounted() {
        }
    }

</script>

<style scoped>
    .row2,
    .sure {
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .row .code-ipt {
        width: 50%;
        float: left;
    }

    .row .get-code {
        float: right;
    }

    .row .sure {
        width: 100%;
    }

    .row .go-login {
        float: right;
        cursor: pointer;
        color: #475669;
    }
</style>


